section {
  display: flex;
  justify-content: center; }

html {
  height: 100%; }

body {
  font-family: Montserrat, Arial, sans-serif;
  font-size: 16px;
  color: #333;
  text-align: center; }

header {
  background-color: steelblue;
  background-image: linear-gradient(170deg, rgba(0, 0, 0, 0.25), transparent);
  color: white;
  padding: 14px 18px; }

section {
  padding: 14px 18px;
  margin: 8px auto;
  background-image: linear-gradient(170deg, rgba(0, 0, 0, 0.25), transparent); }
  section#searchBox {
    background-color: #46b4af;
    border: none; }
    section#searchBox form {
      text-align: right; }
      section#searchBox form div {
        margin: 4px 0; }
        section#searchBox form div:last-child {
          text-align: center; }
      section#searchBox form label, section#searchBox form input {
        padding: 2px; }
  section.resultsPanel .searchResult {
    display: flex;
    justify-content: space-between;
    width: 100%; }

span, a {
  margin: 0 6px; }

.noShrink {
  flex-shrink: 0; }

.button {
  display: inline-block;
  cursor: pointer; }

.spin {
  animation: spin 0.75s linear infinite;
  display: inline-block; }

@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
